* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0b2434;
  padding: 20px;
  font-family: "Karla", sans-serif;
}

main {
  background-color: #f5f5f5;
  height: 500px;
  max-width: 800px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
}

.title {
  font-size: 40px;
  margin: 0;
}
p {
  margin: 0px;
}
.instructions {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  margin-top: 10px;
  text-align: center;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 50px;
  background-color: white;
}

.roll-dice {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035ff;
  color: white;
  font-size: 1.2rem;
  font-family: "Karla", sans-serif;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
#die {
  display: inline-block;
  padding: 5px;
}

.count {
  border-radius: 2px;
  background-color: lightgray;
  padding: 3px;
}
.footer {
  margin-top: 3rem;
  padding-top: 0rem;
  border-top: 3px solid #585c5b;
}

.footer div {
  color: #bbaa7e;
  text-align: center;
}

.footer p {
  font-size: 20px;
  font-weight: 600;
}

.footer a {
  padding-inline: 2rem;
  font-size: 35px;
  color: #438496;
  text-decoration: none;
}

.footer .with-para {
  padding-inline: 0rem;
  font-size: 19px;
  color: #bbaa7e;
  text-decoration: none;
}
